.App {
  // border: 0.1px solid black;
  // overflow-x: 0;,'Arial Narrow'
  font-family: 'Franklin Gothic Medium', Arial, sans-serif;
  // font-family: Arial
}


.header {

  border-top: 0.25rem solid #e3000c;
  padding-top: 0.25rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #f7e9e9;
    .logo {
      height: 85px;
      width: 210px;
    }
  
}

.burger-menu {
  background-color: #f7e9e9;
  transform: rotate(90deg);
  padding: 2px;
}

@media only screen and (min-width: 600px) {//600px and above
  .container {
    display: grid;
    height: 100vh;
    grid-template-columns: 100%;
    grid-template-rows: 11% 81.25% 6%;
    grid-template-areas:
      "header header header header header"
      "main main main main main"
      "footer footer footer footer footer";

    justify-content: center;
  }

  header {
    grid-area: header;
    /* background-color: aqua; */
  }
  footer {
    grid-area: footer;
    /* background-color: brown; */
  }
  main {
    grid-area: main;
    overflow-y: scroll;
    display: flex;
    justify-content: center;
  }
  .home-page-images{
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  .burger-menu {
    display: none;
  }
}


@media only screen and (max-width: 600px) {//600px and below
    main {
    // grid-area: main;
    overflow-y: scroll;
    display: flex;
    justify-content: center;
  }
  .body {
    display: none;
  }
  .home-page {
    width: 90%;
  }
  .header {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .menu {
      ul {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        padding-left: 0px;
        li{
          margin-top:0.75rem;
        }
    }
  }
  .select-component{ 
    margin-top: 0;
    align-items: flex-start;
    button {
      padding-left: 0
    }
  }

  // .email-form .about-page .service-page {
  //   width: 75%;
  // }
}
